import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Projects from './components/projects';
import Skills from './components/skills';
import Welcome from './components/welcome';
import WorkExperiences from './components/work-experiences';
import EducationExperiences from './components/education-experiences';
import PersonalInfo from './components/personal-info';
import Profile from './components/profile';
import ScrollReveal from 'scrollreveal';
 class App extends React.Component {
  componentDidMount(){
    const config = {
      origin: 'right',
      duration: 1000,
      delay: 500,
      distance: '500px',
      scale: 1,
      easing: 'ease',
      reset: true,
    }
  }
  
  // //  ScrollReveal().reveal([this.refs.appref, this.refs.slider], config );
  //   ScrollReveal().reveal('p, header', config );
  // }

  render (){
    const { classes} = this.props;
    return (
      <div className={clsx(classes.root, 'app-wrapper')}>
        <Profile />
        <div className={clsx(classes.content, 'main-content')}>
          <Welcome />
          <PersonalInfo />
          <Skills />
          <WorkExperiences />
          <EducationExperiences />
          <Projects />
        </div>
      </div>
    );
  }
}
const styles = theme => ({
  root: {
    display: 'flex',
    margin: '15px',
    flexDirection: 'column',
    [theme.breakpoints.up('sm')]: {
      flexDirection: 'row'
    },
  },
  content: {
    flexGrow: 1,
    overflow: 'auto',
    [theme.breakpoints.up('sm')]: {
      height: 'calc(100vh - 30px)',
    },
  },
});
export default withStyles(styles)(App);