import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import {Avatar} from '@material-ui/core';
import {Typography} from '@material-ui/core';

class Timelines extends React.Component {
    render (){
      const { classes, list} = this.props;
      return (
        <div className={classes.timelines}>
          {list.map((item, index) => (
             <Item 
             key={index} 
             children={item.children}
             job={item.job} 
             company={item.company} 
             website={item.website} 
             logo={item.logo} 
             content={item.content} 
             date={item.date} />
          ))}
        </div>
       
      );
    }
  }
  const styles = theme => ({
    timelines: {
        marginLeft: '150px',
        position: 'relative',
        '&::before': {
            content: '""',
            position: 'absolute',
            top: '10px',
            left: 0,
            width: '6px',
            height: 'calc(100% - 20px)',
            backgroundColor: '#e6e6e6'
          }
    },
   
  });
  export default withStyles(styles)(Timelines);

  function Item (props){
    const {date, job, company, content, website, logo, children} = props;
    const classes = useStyles();
    return (
      <div className={classes.item}>
        {date && (<span className={classes.date}>{date}</span>)}
        <div  className={classes.content}>
        {job && (<Typography variant="h6" style={{color: 'rgb(220, 0, 78)'}} gutterBottom>{job} </Typography>)}
        {company && (<a href={website} className={classes.com}>
            <Avatar alt={company} src={logo} className={classes.mr}/>
            <Typography variant="subtitle1" style={{fontWeight: '400'}}gutterBottom>{company}</Typography>
          </a>)}
        {content && ( <p className={classes.mt}>{content}</p>)}
        {children && (children)}
        </div>
      </div>
    )
  }

  const useStyles = makeStyles(theme => ({
    item: {
      position: 'relative',
    },
    date: {
        position: 'absolute',
        top: 0,
        left:'-150px',
        maxWidth: '145px'
    },
    content: {
        paddingLeft: theme.spacing(3),
        paddingBottom: theme.spacing(3),
        '&::before': {
            content: '""',
            position: 'absolute',
            top: '10px',
            left: '-2px',
            width: '10px',
            height: '10px',
            borderRadius: '50%',
            backgroundColor: 'rgb(220, 0, 78)'
          }
    },
    com: {
      display: 'flex',
      alignItems: 'center'
    },
    mr: {
      marginRight: theme.spacing(2)
    },
    mt: {
      marginTop: theme.spacing(2)
    }
  }));
