import React from 'react';
import Timelines from '../timelines';
import Title from '../title';
import apac from '../../assets/images/apac.png';
import mof from '../../assets/images/mof.png';
import { withStyles, makeStyles } from '@material-ui/core/styles';
class WorkExperiences extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          list: [
            {
              date: 'Dec 2017 - Mar 2020',
              job: 'Developer',
              company: 'APAC-Consulting',
              website: 'http://apac-consulting.net/',
              logo: apac,
              children: <ApacCom />,
              content: 'APAC Consulting is a global IT Outsourcing firm. The company is a trusted partner providing tailored IT solutions to startups, matured businesses, governments and institutions.'
            },
            {
              date: 'Jul 2014 - Mar 2018',
              job: 'Sales',
              company: 'BIOSMASS Viet Nam',
              website: 'http://biangonnhat.com/',
              logo: 'http://biangonnhat.com/upload/hinhanh/logo-9138_219x78.png',
              children: (<div><p>Common jobs are:</p><ul style={{marginLeft: '15px'}}><li>Sales consultant</li><li>Shipper</li><li>Vehicle warden</li><li>Waiter</li></ul></div>),
              content: 'BIOSMASS Viet Nam is the most popular beer and wine shop. The shop specialise in Belgian, Czechs and Dutch craft beer. And specialise in Italian, Chilen, American and Australian wine.'
            },
            {
                date: 'Dec 2013 - Jan 2014',
                job: 'Waiter',
                company: 'MOF Japanese Dessert Coffe',
                website: '',
                logo: mof,
                children: (<p>Common jobs are cleaning, ordering food and carrying food.</p>),
                content: 'MOF is a modern japanese restaurant coffe.'
            },
          ]
        }
      }
    render (){
      return (
        <section>
            <Title text="Work Experience"/>
            <Timelines list={this.state.list}/>
        </section>
      );
    }
  }
  export default WorkExperiences;

  const useStyles = makeStyles(theme => ({
    parentList: {
      listStyle: 'none',
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
      '& li': {
        marginBottom: theme.spacing(1),
      }
    },
    childList: {
      marginLeft: theme.spacing(3),
    },
  }));
