import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import { ListItemIcon , List , ListItem , ListItemText , Typography, AppBar , Toolbar  , Grid, Dialog , Slide, Button    } from '@material-ui/core';
import Title from '../title';
import bbaghome from '../../assets/images/bbaghome.jpg';
import bbagshop from '../../assets/images/bbagshop.jpg';
import bbagsingle from '../../assets/images/bbagsingle.jpg';
import bemohome from '../../assets/images/bemohome.jpg';
import bemosingle from '../../assets/images/bemosingle.jpg';
import bissblock from '../../assets/images/bissblock.jpg';
import bisshome from '../../assets/images/bisshome.jpg';
import bissproject from '../../assets/images/bissproject.jpg';
import budhome from '../../assets/images/budhome.jpg';
import budform from '../../assets/images/budform.jpg';
import chatfeedback from '../../assets/images/chatfeedback.jpg';
import chatmessage from '../../assets/images/chatmessage.jpg';
import chatshop from '../../assets/images/chatshop.jpg';
import chatsignin from '../../assets/images/chatsignin.jpg';
import chatme from '../../assets/images/chattme.jpg';
import doorigoform from '../../assets/images/doorigo_form.jpg';
import doorigohome from '../../assets/images/doorigo_home.jpg';
import jlshome from '../../assets/images/jlshome.jpg';
import stmathform from '../../assets/images/stmath_form.jpg';
import visahuntadmin from '../../assets/images/visahuntadmin.jpg';
import visahuntform from '../../assets/images/visahuntform.jpg';
import visahunthome from '../../assets/images/visahunthome.jpg';
import xappadminhome from '../../assets/images/xappadminhome.jpg';
import xappadminlogin from '../../assets/images/xappadminlogin.jpg';
import xappclient from '../../assets/images/xappclient.jpg';
import xappclienthome from '../../assets/images/xappclienthome.jpg';
import xappcompany from '../../assets/images/xappcompany.jpg';
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });
class Projects extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            selected: null,
            list: [
                  {
                    images: [bbaghome, bbagshop, bbagsingle],
                    title: 'Bbag Shop',
                    platform: 'Wordpress + woocomerce',
                  },
                  {
                    images: [bemohome, bemosingle],
                    title: 'Bemo Store',
                    platform: 'Bootstrap',
                 },
                 {
                  images: [bissblock, bisshome, bissproject],
                    title: 'Biss Theme',
                    platform: 'Wordpress Rest Api + Aurelia',
                 },
                 {
                  images: [budhome, budform],
                  title: 'Bud App',
                  platform: 'Reactjs + Laravel',
                }, 
                {
                  images: [chatsignin, chatme, chatfeedback, chatmessage, chatshop],
                  title: 'Chat App',
                  platform: 'React Native + Asp.net',
                }, 
                {
                  images: [doorigoform, doorigohome],
                  title: 'Doorigo',
                  platform: 'Reactjs + Codeigniter',
                }, 
                {
                  images: [jlshome],
                  title: 'JLS Portal',
                  platform: 'Nuxt + Asp.net',
                },
                {
                  images: [stmathform],
                  title: 'ST Math',
                  platform: 'Aurelia',
                },
                {
                  images: [visahuntadmin, visahuntform, visahunthome],
                  title: 'Visahunt',
                  platform: 'Angular 8 + Asp.net',
                },
                {
                  images: [xappclienthome, xappadminlogin, xappclient, xappcompany, xappadminhome],
                  title: 'Job Portal',
                  platform: 'Angular 8 + Asp.net',
                },
            ]
        }
    };

    render (){
      const { classes, title, number} = this.props;
      const {list, open, selected} = this.state;
        return (
            <section>
                <Title text="Projects"/>
                <Grid container justify="center" spacing={3}>
                {list.map((tile, index) => (
                    <Grid key={index}  item xs={6} md={4}>
                        <div className={classes.item} onClick={() => this.handleClick(tile)}>
                          
                            <span className={clsx(classes.title, 'project-title')}>{tile.platform}</span>
                            <span className={clsx(classes.platform, 'project-platform')}>{tile.title}</span>
                         
                            <div className={clsx(classes.shadow, 'project-shadow')}></div>
                            <img  src={tile.images[0]} alt={tile.title} className={clsx(classes.image,'project-image' )}/> 
                        </div>
                    </Grid>
                ))}
                </Grid>
                <Dialog fullScreen open={open} onClose={this.handleClose} TransitionComponent={Transition}>
                    <div style={{display: 'flex', padding: '10px',marginBottom: '20px', border: 'solid 1px #e6e6e6', justifyContent: 'space-between', alignItems: 'center'}}>
                    {selected && selected.title && (<h3>{selected.title}</h3>)}
                      <Button autoFocus color="inherit"  onClick={this.handleClose}>
                          <span className="material-icons">close</span>
                      </Button>
                    </div>
                    <div className="product-categories">
                              <ul>
                                {selected && selected.images && selected.images.map((img, index) => {
                                  let width = 100/selected.images.length ;
                                  return (
                                    <li style={{width: width + '%'}} key={index}>
                                    <img src={img}  className="d-block content w-100" />
                                    </li>
                                  )
                                })}
                              </ul>
                            </div>
                </Dialog>
            </section>
        );
    }

    handleClick = (item) => {
        this.setState({selected: item, open: true})
    }

    handleClose = () => {
        this.setState({open: false, selected: null})
    }
  }
  const styles = theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
      },
      gridList: {
        width: '100%',
        transform: 'translateZ(0)',
      },
    projects: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
      },
      item: {
        width: '100%',
        height: 200,
        position:'relative',
        cursor: 'pointer',
        transition: 'all 0.4s',
        borderRadius:'10px',
        border: 'solid 1px #e6e6e6',
        overflow: 'hidden',
        textAlign: 'center',
        '&:hover .project-title': {
            opacity: 1,
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
        },
        '&:hover .project-platform': {
          opacity: 0,
      },
        '&:hover .project-shadow': {
            opacity: 1,
        },
        '&:hover .project-image': {
            transform: 'scale(1.2)',
        },
      },
      title: {
        position:'absolute',
        top: '70%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        transition: 'all 0.4s',
        opacity: 0,
        zIndex: 3,
        color: 'rgb(220, 0, 78)',
        fontWeight: 700,
        fontSize: '1.2em'
      },
      platform: {
        position:'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        transition: 'all 0.4s',
        zIndex: 3,
        color: 'rgb(220, 0, 78)',
        fontWeight: 700,
        fontSize: '1.2em'
      },
      shadow: {
        position:'absolute',
        width: '100%',
        height: '100%',
        top: 0,
        left: 0,
        transition: 'all 0.4s',
        backgroundColor: 'rgba(255, 255, 255, 0.7)',
        opacity: 0,
        zIndex: 2
      },
      image: {
        width: '100%',
        height: '100%',
        objectFit: 'cover',
        transition: 'all 0.4s',
        borderRadius: '10px',
      },
      appBar: {
        position: 'relative',
      },
  });
  export default withStyles(styles)(Projects);
