import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import {Grid , List , ListItem , Typography   } from '@material-ui/core';
import Rating from '../rating';
import Title from '../title';
import clsx from 'clsx';
class PersonalInfo extends React.Component {
   
    render (){
        const {classes} = this.props;
        const list = [
            {name: 'Full Name', value: 'Nguyen Minh Long'},
            {name: 'D.o.b.', value: '02 July 1991'},
            {name: 'Address', value: '429/26B Le Van Sy, Ward 12, District 3, Ho Chi Minh city, Viet Nam'},
            {name: 'Email', value: 'long.ngyen91@gmail.com'},
            {name: 'Phone', value: '0396 945 551'},
            {name: 'Experience', value: '+2 years'},
          ]
          return (
            <section>
                <Grid container spacing={3}>
                  <Grid item xs sm={6}>
                  <Title text="Personal Info"/>
                  <List className={classes.list}>
                    {list.map((item, index) => (
                       <ListItem key={index}  className={classes.item}>
                        <span className="sub-title">{item.name}</span>
                        <span>{item.value}</span>
                       </ListItem>
                    ))}
                  </List>
                  </Grid>
                <Grid item xs sm={6}>
                  <Title text="Languages"/>
                  <Rating number={3} title="English"/>
                </Grid>
              </Grid>
            </section>
          );
    }
  }
  const styles = theme => ({
    mb: {
        marginBottom: theme.spacing(3),
    },
    item: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'flex-start',
        padding: 0
    },
    list: {
        padding: 0
    }
  });
  export default withStyles(styles)(PersonalInfo);
