import React from 'react';
import PropTypes from 'prop-types';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import {Typography , Box , Grid, Paper} from '@material-ui/core';
import Title from '../title';
import {ChartCom} from '../chart';

  const useStyles = makeStyles(theme => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      // margin: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },
    title: {
      textAlign: 'center',
    }
  }));
  
export default function Skills () {
    const classes = useStyles();
    return (
        <section>
            <Title text="My Skills"/>
            <Grid container >
              <Grid item xs={12} md={6}>
             
                <Box className={classes.paper}>
                <ChartCom data={
              [
                {
                  subject: 'Photoshop', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'Bootstrap', A: 90, B: 90, fullMark: 100,
                },
                {
                  subject: 'Angular', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'Reactjs', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'React Native', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'Aurelia', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'Vuejs', A: 70, B: 70, fullMark: 100,
                },
              ]
            }/>
                </Box>
                <Typography variant="body1"  className={classes.title}>
                Front-End
              </Typography>
              </Grid>
              <Grid item xs={12} md={6}>
             
                <Box className={classes.paper}>
                <ChartCom data={[
                 {
                  subject: 'Wordpress', A: 90, B: 90, fullMark: 100,
                },
                {
                  subject: 'Codeigniter', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'Laravel', A: 80, B: 80, fullMark: 100,
                },
                {
                  subject: 'Asp.net', A: 60, B: 60, fullMark: 100,
                }
            ]}/>
                </Box>
                <Typography variant="body1"  className={classes.title}>
                Back-End
              </Typography>
              </Grid>
        </Grid>
           
         
        </section>
    );
}

