import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import avatar from '../../assets/images/avatar.jpg';
import clsx from 'clsx';
import cv from '../../assets/cv.docx';
class ProFile extends React.Component {
   
    render (){
      const { classes} = this.props;
      return (
        <div className={classes.info}>
          <div  className={clsx(classes.avatarWrapper, 'user-info')}>
              <img  className={classes.avatar} src={avatar} />
          </div>
          <div className={classes.infoBot}>
            <h3>Long Nguyen</h3>
            <p>Front End Software Developer</p>
            <a href={cv} download className={classes.butonDownload}>Download CV</a>
          </div>
        </div>
      );
    }
  }
  const styles = theme => ({
    mb: {
      marginBottom: theme.spacing(3)
    },
    info: {
      width: '100%',
      height: 'calc(100vh - 30px)',
      borderRadius: '10px',
      boxShadow: '10px 10px 15px rgba(0,0,0,.05)',
      backgroundColor: '#e6e6e6',
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
      [theme.breakpoints.up('sm')]: {
        maxWidth: 'calc(200px + 10vmin)',
        marginRight: '15px',
        minWidth: 'calc(200px + 10vmin)'
      },
    },
    avatar: {
      width: '100%',
      height: '100%',
      objectFit: 'cover'
    },
    avatarWrapper: {
      width: '100%',
      height: '80%',
      overflow: 'hidden',
      backgroundColor: 'white',
      position:'relative',
      [theme.breakpoints.up('sm')]: {
        height: '50%',
      },
    },
    infoBot: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent:'center',
      alignItems: 'center'
    },
    butonDownload: {
      border: 'solid 2px rgb(220, 0, 78)',
      borderRadius: '5px',
      fontWeight: 500,
      marginTop: '15px',
      padding: '5px 10px',
      display: 'block',
      color: 'rgb(220, 0, 78)'
    }
  });
  export default withStyles(styles)(ProFile);
