import React from 'react';
import Timelines from '../timelines';
import Title from '../title';
import khtn from '../../assets/images/khtn.png';
class EducationExperiences extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          list: [
            {
              date: 'May 2016 - Dec 2016',
              job: 'Web Designer Certificate',
              company: 'Ho Chi Minh University Of Natural Science - Computer Science Center',
              website: 'https://csc.edu.vn/',
              logo: 'http://biangonnhat.com/upload/hinhanh/logo-9138_219x78.png',
              content: 'Web Designer Certificate'
            },
            {
              date: 'Sep 2011 - July 2014',
              job: 'Colleges',
              company: 'Ho Chi Minh University Of Natural Science',
              website: 'https://www.hcmus.edu.vn/',
              logo: khtn,
              content: 'VNUHCM-University of Science (VNUHCM-US; formerly known as University of Sciences) is one of the six esteemed members of the Vietnam National University System in Ho Chi Minh City.'
            },
           
          ]
        }
      }
    render (){
      return (
        <section>
            <Title text="Education Experience"/>
            <Timelines list={this.state.list}/>
        </section>
      );
    }
  }
  export default EducationExperiences;
